#rooms {
  height: calc( 100vh - 55px);
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;
  margin-top: 55px;
  box-sizing: border-box;
  padding: 20px;
  padding-right: 30px; }
  #rooms .room:hover > .hover-overlay {
    background: rgba(0, 0, 0, 0); }
  #rooms .room {
    width: calc(33.333% - 10px);
    margin-left: 10px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 8px;
    background-position: center;
    background-size: cover;
    box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.4);
    position: relative;
    margin-bottom: 10px; }
    #rooms .room .hover-overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.1);
      border-top-left-radius: 6px;
      border-top-right-radius: 6px;
      color: #FBCA43;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 25px;
      font-family: 'Comfortaa'; }
    #rooms .room .room-header {
      height: 50px;
      border-top-left-radius: 6px;
      border-top-right-radius: 6px;
      padding: 5px;
      padding-left: 10px;
      padding-right: 10px;
      box-sizing: border-box;
      font-family: 'Comfortaa';
      color: white;
      display: flex;
      justify-content: space-between;
      background: linear-gradient(0deg, rgba(2, 0, 36, 0) 0%, rgba(0, 0, 0, 0.8) 100%); }
      #rooms .room .room-header .room-name {
        display: flex;
        align-self: flex-start;
        align-items: center; }
        #rooms .room .room-header .room-name .on-dot {
          width: 10px;
          height: 10px;
          border-radius: 10px;
          background: #62f442;
          margin-right: 10px; }
      #rooms .room .room-header .room-category {
        color: #FBCA43;
        font-family: "comfortaa";
        font-size: 20px;
        user-select: none; }
  #rooms .joins {
    position: relative;
    display: flex;
    padding: 10px;
    box-sizing: border-box;
    color: #FBCA43;
    font-family: 'Comfortaa';
    font-size: 15px; }
    #rooms .joins .joins-left {
      width: 170px;
      align-self: flex-end; }
      #rooms .joins .joins-left .joins-title {
        margin-bottom: 5px;
        user-select: none; }
      #rooms .joins .joins-left .join-btns .join-btn:hover {
        border: 2px solid white;
        color: white; }
      #rooms .joins .joins-left .join-btns .join-btn {
        outline: none;
        margin-left: 5px;
        background: black;
        font-family: 'Comfortaa';
        cursor: pointer;
        border: 2px solid #FBCA43;
        border-radius: 3px;
        color: #FBCA43; }
    #rooms .joins .joins-right {
      width: calc(100% - 170px);
      display: flex;
      align-items: flex-end;
      height: 60px;
      justify-content: flex-end;
      flex-direction: column; }
      #rooms .joins .joins-right .room-clients {
        display: flex;
        width: 100%;
        justify-content: flex-end; }
        #rooms .joins .joins-right .room-clients .room-client {
          border: 1px solid #FBCA43;
          width: 30px;
          height: 30px;
          border-radius: 30px;
          margin-right: 5px;
          position: relative; }
        #rooms .joins .joins-right .room-clients .room-client:hover > .simpleProfileModal {
          display: block; }
      #rooms .joins .joins-right .room-viewers {
        display: flex;
        width: 100%;
        justify-content: flex-end; }
        #rooms .joins .joins-right .room-viewers .room-viewer {
          border: 1px solid #F7DF9D;
          width: 20px;
          height: 20px;
          border-radius: 20px;
          margin-right: 5px;
          position: relative; }
        #rooms .joins .joins-right .room-viewers .room-viewer:hover > .simpleProfileModal {
          display: block; }
