.GraffitiTheme {
  background-color: #F4FBFB;
  background-color: #cbd2d2;
  background-color: #ffcd44;
  background-color: #dde4e4;
  background-color: #282828; }

#home {
  height: calc(100vh - 55px);
  display: flex;
  margin-top: 55px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "comfortaa";
  background: url("/assets/surkls_landing.png");
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  z-index: -1; }
  #home #registerTitle {
    margin-top: -70px;
    margin-bottom: 10px;
    font-size: 30px;
    z-index: 20; }
  #home #loginStrategies {
    display: flex;
    justify-content: space-around;
    width: 300px;
    z-index: 20; }
    #home #loginStrategies #twitchStrat {
      background: url("/assets/twitch_btn.png");
      background-position: center;
      background-size: 35px;
      background-repeat: no-repeat; }
    #home #loginStrategies #twitterStrat {
      background: url("/assets/twitter_btn.png");
      background-position: center;
      background-size: 50px;
      background-repeat: no-repeat; }
    #home #loginStrategies #googleStrat {
      background: url("/assets/google_btn.png");
      background-position: center;
      background-size: 25px;
      background-repeat: no-repeat; }
    #home #loginStrategies .loginStrat {
      border: 2px solid #282828;
      height: 35px;
      width: 35px;
      background: white;
      border-radius: 35px;
      margin-bottom: 10px;
      transition: all .2s ease-in; }
    #home #loginStrategies .loginStrat:hover {
      transform: scale(1.1);
      cursor: pointer; }
  #home .form {
    z-index: 20;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
    #home .form .homeInput {
      font-family: 'Didact Gothic';
      background: #282828;
      color: white;
      font-size: 15px;
      outline: none;
      padding-left: 20px;
      width: 450px;
      height: 30px;
      margin-bottom: 20px;
      border-radius: 40px;
      border: none; }
    #home .form #registerBtn {
      border: 2px solid #282828;
      border-radius: 30px;
      background: transparent;
      outline: none;
      height: 30px;
      padding-bottom: 5px;
      padding-left: 7px;
      padding-right: 7px;
      font-size: 16px;
      font-family: 'Didact Gothic'; }
    #home .form #registerBtn:hover {
      color: #FFEBB4;
      border: 2px solid #FFEBB4;
      cursor: pointer; }

.errorColor::-webkit-input-placeholder {
  color: #d84224; }
