#pulloutBg {
  height: 100vh;
  width: 100%;
  position: fixed;
  left: 0px;
  top: 0px;
  background-color: rgba(0, 0, 0, 0);
  z-index: 130;
  overflow: hidden;
  animation: removeOverlay .3s ease-out forwards;
  display: none; }

.pulloutAction {
  animation: pullout .3s ease-out forwards !important; }

.overlayAction {
  animation: overlay .3s ease-out forwards !important; }

#pullout {
  animation: pullin .3s ease-out forwards;
  transform: translateX(-100%);
  height: 100vh;
  width: 260px;
  background-color: #DDE4E4; }

.menuItem:hover {
  background-color: #cbd2d2; }

.menuItem {
  padding: 10px;
  font-size: 17px;
  display: flex;
  font-family: 'Didact Gothic';
  cursor: pointer; }

.menuSurkls {
  width: 100%;
  border-top: 1px solid #303030;
  height: 100px;
  margin-top: 10px;
  overflow-y: scroll; }
  .menuSurkls .menuSurklsHeader {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    font-family: 'Comfortaa'; }

@keyframes overlay {
  100% {
    background-color: rgba(0, 0, 0, 0.4); } }

@keyframes pullout {
  100% {
    transform: translate(0%, 0px); } }

@keyframes removeOverlay {
  0% {
    background-color: rgba(0, 0, 0, 0.4); }
  100% {
    background-color: rgba(0, 0, 0, 0); } }

@keyframes pullin {
  0% {
    transform: translate(0%, 0px); }
  100% {
    transform: translate(-100%, 0px); } }
