/*icons*/
.moveOver {
  animation: roll 1.5s cubic-bezier(0.04, 0.08, 0, 0.87) forwards, spin 1.5s cubic-bezier(0.04, 0.08, 0, 0.87) forwards; }

@keyframes spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(-660deg); } }

@keyframes roll {
  from {
    left: -50px; }
  to {
    left: -200px; } }
