#account-settings {
  width: 100%;
  height: 100%; }
  #account-settings #tabs {
    height: 30px;
    display: flex; }
    #account-settings #tabs .tab {
      width: 120px;
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
      height: 30px;
      margin-left: 5px;
      display: flex;
      background: #242323;
      justify-content: center;
      align-items: center;
      color: #F4FBFB;
      font-family: 'Comfortaa';
      cursor: pointer; }
    #account-settings #tabs .profile-tab {
      border-bottom: 1px solid #242323; }
  #account-settings #tabs > div:first-child {
    margin-left: 0px; }
  #account-settings .tab-win {
    height: calc(100% - 30px);
    margin-top: 1px;
    border-radius: 5px;
    border-top-left-radius: 0px; }
